import React, { Component } from 'react';

import DeleteIcon from '@material-ui/icons/Delete';
import Fab from '@material-ui/core/Fab';
import FormGroup from '@material-ui/core/FormGroup';
import FormLabel from '@material-ui/core/FormLabel';
import AddIcon from '@material-ui/icons/Add';
import Grid from '@material-ui/core/Grid';

import NormalInput from "./NormalInput"

function Alapanyag(props) {
    return (
        <FormGroup row={true}>
            <NormalInput label="Mennyiség"
                placeholder="Add meg a mennyiséget..."
                required={true}
                width={props.width}
                margin={props.margin}
                value={props.mennyiseg}
                onChange={props.changeMennyiseg}
                type="number"
            />
            <NormalInput label="Mértékegység"
                placeholder="Add meg a mértékegységet..."
                required={true}
                width={props.width}
                margin={props.margin}
                value={props.mertekegyseg}
                onChange={props.changeMertekegyseg}
                type="text"
            />
            <NormalInput label="Alapanyag"
                placeholder="Add meg az alapanyagot..."
                required={true}
                width={props.width}
                margin={props.margin}
                value={props.alapanyag}
                onChange={props.changeAlapanyag}
                type="text"

            />
            <Fab style={{ marginTop: 20 }} color="secondary" aria-label="Delete" size="small" onClick={props.deleteAlapanyag}>
                <DeleteIcon />
            </Fab>
        </FormGroup>
    );
}

export function getDefaultAlapanyag(nextId) {
    return { id: nextId, name: '', mennyiseg: '', mertekegyseg: '' };
}
export function getDefaultHozzavalo(nextId) {
    return {
        id: nextId,
        name: "",
        counter: 1,
        alapanyagok: [
            getDefaultAlapanyag(0),
        ]
    };
}

class Hozzavalok extends Component {

    constructor() {
        super();
        this.state = {
            hozzavalokCounter: 2,
            hozzavalok: [
                {
                    id: 1,
                    name: "",

                }
            ]
        };
    }


    removeHozzavalo = (index, e) => {
        const hozzavalok = Object.assign([], this.state.hozzavalok);
        hozzavalok.splice(index, 1);
        this.setState({ hozzavalok: hozzavalok });
    }

    addHozzavalo(indexH) {
        const hozzavalok = [...this.state.hozzavalok, getDefaultHozzavalo(this.state.hozzavalokCounter)];
        this.setState({ hozzavalok: hozzavalok, hozzavalokCounter: this.state.hozzavalokCounter + 1 });
    }


    render() {

        return (
            <div>
                <FormLabel >Hozzávalok:</FormLabel>
                <ul>
                    {
                        this.props.hozzavalok.map((hozzavalo, indexH) => {
                            const tmp = indexH + 1 + ". hozzavaló csoport"
                            return (
                                <ul key={indexH}>
                                    <NormalInput
                                        label={tmp}
                                        placeholder={tmp}
                                        required={true}
                                        width="100%"
                                        margin="normal"
                                        value={hozzavalo.name}
                                        onChange={(e) => this.props.changeHozzavaloNev(hozzavalo.id, e)}
                                    />

                                    <Fab style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }} color="secondary" aria-label="Delete" size="small" onClick={e => this.props.removeHozzavalo(indexH, e)}>
                                        <DeleteIcon />
                                    </Fab>
                                    <div>
                                        <Grid item xs={12} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                                            <FormLabel>Alapanyagok:</FormLabel>
                                        </Grid>
                                        <ul>
                                            {
                                                hozzavalo.alapanyagok.map((alapanyag, indexA) => {
                                                    return (
                                                        <div key={indexA}>
                                                            <Alapanyag
                                                                deleteAlapanyag={e => this.props.removeAlapanyag(indexH, indexA, e)}

                                                                changeMertekegyseg={e => this.props.changeMertekegyseg(indexH, alapanyag.id, e)}
                                                                mertekegyseg={alapanyag.mertekegyseg}

                                                                changeMennyiseg={e => this.props.changeMennyiseg(indexH, alapanyag.id, e)}
                                                                mennyiseg={alapanyag.mennyiseg}

                                                                changeAlapanyag={e => this.props.changeAlapanyag(indexH, alapanyag.id, e)}
                                                                alapanyag={alapanyag.name}
                                                                width="80%"
                                                                margin="normal"
                                                                key={alapanyag.id}
                                                            />

                                                        </div>);

                                                })
                                            }
                                            <Grid item xs={12} style={{ marginTop: 20 }}>

                                                <Fab
                                                    aria-label="Add"
                                                    size="small"
                                                    color="secondary"
                                                    onClick={(e) => this.props.addAlapanyag(indexH, e)}>
                                                    <AddIcon />
                                                </Fab>
                                            </Grid>
                                        </ul>

                                    </div>
                                </ul>
                            )
                        })
                    }
                    <Grid item xs={12} style={{ marginTop: 20 }}>
                        <Fab
                            aria-label="Add"
                            size="small"
                            color="secondary"
                            onClick={() => this.props.addHozzavalo()}
                        >
                            <AddIcon />
                        </Fab>
                    </Grid>
                </ul>
            </div>
        )
    }
}
export default Hozzavalok;