
import React from "react";

import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormGroup from '@material-ui/core/FormGroup';
import Checkbox from '@material-ui/core/Checkbox';
import FormLabel from '@material-ui/core/FormLabel';

import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

const styles = {
    root: {
        color: '#445a84',
        '&$checked': {
            color: '#2E3B55',
        },
    },
    checked: {},
};

class MultipleChoose extends React.Component {
    render() {
        var checks = [];
        for (var i = 0; i < this.props.labels.length; i++) {
            var has = false;
            for (var j = 0; j < this.props.checks.length; j++) {
                if (this.props.labels[i].localeCompare(this.props.checks[j]) === 0) {
                    has = true;
                }
            }
            if (has) {
                checks.push(true);
            } else {
                checks.push(false);
            }
        }


        const checkboxItems = this.props.labels.map((oneLabel, index) =>

            <FormControlLabel key={index}
                control={<Checkbox
                    checked={checks[index]}
                    color="primary"
                    onChange={e => this.props.onChange(oneLabel, e)} />}
                label={oneLabel} />

        );
        return (
            <div>
                <FormLabel >{this.props.title}</FormLabel>
                <FormGroup row={true} className="form-group">
                    {checkboxItems}
                </FormGroup>
            </div>
        );
    }
}

MultipleChoose.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(MultipleChoose);
