import React from "react";

import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';

import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';

const styles = theme => ({
    paper: {
         textAlign: 'justify',
         padding: theme.spacing.unit * 2,
       
    },
});

function getArrayStr(data) {
    var str = "";
    for (var j = 0; j < data.length; j++) {
        if (j === data.length - 1) {
            str = str + data[j].toString();
        } else {
            str = str + data[j].toString() + ", ";
        }
    }
    return (
        { str: str }
    );
}
function getElkeszitesStr(data) {
    return (
        data.map((item, index) => {
            return <ul key={index}>
                <p>{index + 1}. lépés:</p>
                <p>{item.text}</p>
            </ul>
        }
        )
    );
}
function getHozzavalokStr(data) {
    return (
        data.map((item, index) => {
            return (
                <ul key={index}>
                    {item.name.toString()}
                    {item.alapanyagok.map((alapanyag, index) => {
                        return (
                            <ul key={index}>
                                {alapanyag.mennyiseg}
                                {" "}
                                {alapanyag.mertekegyseg}
                                {" "}
                                {alapanyag.name}
                            </ul>
                        );
                    })
                    }

                </ul>
            )
        }
        )
    );
}

class RecipePage extends React.Component {

    render() {
        const { classes } = this.props;
        return (
            <Paper className={this.props.ownClassName} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                <Grid container spacing={24}>
                    <Grid item xs style={{ marginTop: 20, marginLeft: 20 }}>
                        <h2>{this.props.state.receptCim}</h2>
                        <h5>Évszakok: {getArrayStr(this.props.state.evszak).str}</h5>
                        <h5>Ünnepek: {getArrayStr(this.props.state.unnep).str}</h5>
                        <h5>Diéta: {getArrayStr(this.props.state.dieta).str}</h5>
                        <h5>Menü: {getArrayStr(this.props.state.menu).str}</h5>
                        <h5>Napszak: {getArrayStr(this.props.state.napszak).str}</h5>
                        <h5>Technika: {getArrayStr(this.props.state.technika).str}</h5>
                        <h5>Elkészítési idő: {this.props.state.elkeszitesiIdo} perc</h5>
                        <h5>Kalória: {this.props.state.kaloria} kcal</h5>
                        <h5>Hozzávalok: {getHozzavalokStr(this.props.state.hozzavalok)}</h5>
                        
                    </Grid>
                    <Grid item xs style={{ marginTop: 20, marginRight: 20 }}>
                        <img src={this.props.state.file} width="100%" alt=""></img>
                    </Grid>
                </Grid>
                <Grid item xs style={{ marginTop: 20, marginLeft: 20 }}>
                <h5 className={classes.paper} >Elkészítés: {getElkeszitesStr(this.props.state.elkeszites.step)}</h5>
                </Grid>
            </Paper>
        );
    };
}

RecipePage.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(RecipePage);