
import React, { Component } from 'react';

import NormalInput from "./NormalInput"
import Hozzavalok from "./Hozzavalok"
import Elkeszites from "./Elkeszites";
import MultipleChoose from "./MultipleChoose";
import { getDefaultAlapanyag, getDefaultHozzavalo } from "./Hozzavalok.js"

import * as Constans from '../Constans';

import axios from 'axios';


import { FilePond, registerPlugin } from 'react-filepond';
import 'filepond/dist/filepond.min.css';
import FilePondPluginFileValidateType from 'filepond-plugin-file-validate-type';
import FilePondPluginImageResize from 'filepond-plugin-image-resize';
import FilePondPluginImagePreview from 'filepond-plugin-image-preview';
import 'filepond-plugin-image-preview/dist/filepond-plugin-image-preview.min.css';
import FilePondPluginFileValidateSize from 'filepond-plugin-file-validate-size';

import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import PropTypes from 'prop-types';
import { BrowserView, isBrowser } from "react-device-detect";

import { withStyles } from '@material-ui/core/styles';
import '../split.css';
import CustomDialog from './CustomDialog';
import RecipePage from './RecipePage';


const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        padding: theme.spacing.unit * 2,
        // textAlign: 'center',
        color: theme.palette.text.secondary,
    },
});


registerPlugin(
    FilePondPluginImageResize, FilePondPluginImagePreview, FilePondPluginFileValidateSize, FilePondPluginFileValidateType);

const gridSytle = { marginLeft: 20, marginRight: 20 };


function getMobilOrBrowserSplit() {
    if (isBrowser) {
        return "split left";
    } else {
        return "full";
    }
}
function clearRecipe() {
    return (
        {
            file: "",
            fileUrl: "",
            fileID: "",
            receptCim: "",
            elkeszitesiIdo: "",
            kaloria: "",
            evszak: [],
            unnep: [],
            dieta: [],
            menu: [],
            napszak: [],
            technika: [],
            hozzavalokCounter: 2,
            hozzavalok: [
                {
                    id: 1,
                    name: "",
                    counter: 1,
                    alapanyagok: [
                        getDefaultAlapanyag(0),
                    ]
                }
            ],
            elkeszites: {
                counter: 2,
                step: [
                    {
                        id: 1,
                        text: "",
                    }
                ]
            }
        }
    );
}
class Create extends Component {
    constructor() {
        super();
        this.state = {
            okMsg: "",
            progValue: 0,
            dialogMsg: "",
            dialogOpen: false,
            file: "",
            fileUrl: "",
            fileID: "",
            receptCim: "",
            elkeszitesiIdo: "",
            kaloria: "",
            evszak: [],
            unnep: [],
            dieta: [],
            menu: [],
            napszak: [],
            technika: [],
            hozzavalokCounter: 2,
            hozzavalok: [
                {
                    id: 1,
                    name: "",
                    counter: 1,
                    alapanyagok: [
                        getDefaultAlapanyag(0),
                    ]
                }
            ],
            elkeszites: {
                counter: 2,
                step: [
                    {
                        id: 1,
                        text: "",
                    }
                ]
            }
        };

    }
    onReceptCimChange(e) {
        this.setState({ receptCim: e.target.value });
    }
    onElkeszitesiIdoChange(e) {
        this.setState({ elkeszitesiIdo: e.target.value });
    }
    onKaloriaChange(e) {
        this.setState({ kaloria: e.target.value });
    }
    onEvszakChange(label, e) {
        const evszak = this.state.evszak
        let index
        if (e.target.checked) {
            evszak.push(label)
        } else {
            index = evszak.indexOf(label)
            evszak.splice(index, 1)
        }
        evszak.sort();
        this.setState({ evszak: evszak })
    }
    onUnnepChange(label, e) {
        const unnep = this.state.unnep
        let index
        if (e.target.checked) {
            unnep.push(label)
        } else {
            index = unnep.indexOf(label)
            unnep.splice(index, 1)
        }
        unnep.sort();
        this.setState({ unnep: unnep })
    }
    onDietaChange(label, e) {
        const dieta = this.state.dieta
        let index
        if (e.target.checked) {
            dieta.push(label)
        } else {
            index = dieta.indexOf(label)
            dieta.splice(index, 1)
        }
        dieta.sort();
        this.setState({ dieta: dieta })
    }
    onMenuChange(label, e) {
        const menu = this.state.menu
        let index
        if (e.target.checked) {
            menu.push(label)
        } else {
            index = menu.indexOf(label)
            menu.splice(index, 1)
        }
        menu.sort();
        this.setState({ menu: menu })
    }
    onNapszakChange(label, e) {
        const napszak = this.state.napszak
        let index
        if (e.target.checked) {
            napszak.push(label)
        } else {
            index = napszak.indexOf(label)
            napszak.splice(index, 1)
        }
        napszak.sort();
        this.setState({ napszak: napszak })
    }
    onTechnikaChange(label, e) {
        const technika = this.state.technika
        let index
        if (e.target.checked) {
            technika.push(label)
        } else {
            index = technika.indexOf(label)
            technika.splice(index, 1)
        }
        technika.sort();
        this.setState({ technika: technika })
    }
    removeHozzavalo = (index, e) => {
        const hozzavalok = Object.assign([], this.state.hozzavalok);
        hozzavalok.splice(index, 1);
        this.setState({ hozzavalok: hozzavalok });
    }
    removeAlapanyag = (indexH, indexA, e) => {
        const hozzavalok = Object.assign([], this.state.hozzavalok);
        hozzavalok[indexH].alapanyagok.splice(indexA, 1);
        this.setState({ hozzavalok: hozzavalok });
    }
    addHozzavalo() {
        const hozzavalok = [...this.state.hozzavalok, getDefaultHozzavalo(this.state.hozzavalokCounter)];
        this.setState({ hozzavalok: hozzavalok, hozzavalokCounter: this.state.hozzavalokCounter + 1 });
    }
    addAlapanyag(indexH, e) {
        const alapanyagok = [...this.state.hozzavalok[indexH].alapanyagok, getDefaultAlapanyag(this.state.hozzavalok[indexH].counter)];
        const hozzavalok = Object.assign([], this.state.hozzavalok);
        hozzavalok[indexH].alapanyagok = alapanyagok;
        hozzavalok[indexH].counter = hozzavalok[indexH].counter + 1;
        this.setState({ hozzavalok: hozzavalok });
    }
    changeMertekegyseg = (indexH, idA, event) => {
        const indexA = this.state.hozzavalok[indexH].alapanyagok.findIndex((alapanyagItem) => {
            return (alapanyagItem.id === idA);
        })
        const alapanyag = Object.assign({}, this.state.hozzavalok[indexH].alapanyagok[indexA]);
        alapanyag.mertekegyseg = event.target.value;
        const hozzavalok = Object.assign([], this.state.hozzavalok);
        hozzavalok[indexH].alapanyagok[indexA] = alapanyag;
        this.setState({ hozzavalok: hozzavalok });
    }
    changeMennyiseg = (indexH, idA, event) => {
        const indexA = this.state.hozzavalok[indexH].alapanyagok.findIndex((alapanyagItem) => {
            return (alapanyagItem.id === idA);
        })
        const alapanyag = Object.assign({}, this.state.hozzavalok[indexH].alapanyagok[indexA]);
        alapanyag.mennyiseg = event.target.value;
        const hozzavalok = Object.assign([], this.state.hozzavalok);
        hozzavalok[indexH].alapanyagok[indexA] = alapanyag;
        this.setState({ hozzavalok: hozzavalok });
    }
    changeAlapanyag = (indexH, idA, event) => {
        const indexA = this.state.hozzavalok[indexH].alapanyagok.findIndex((alapanyagItem) => {
            return (alapanyagItem.id === idA);
        })
        const alapanyag = Object.assign({}, this.state.hozzavalok[indexH].alapanyagok[indexA]);
        alapanyag.name = event.target.value;
        const hozzavalok = Object.assign([], this.state.hozzavalok);
        hozzavalok[indexH].alapanyagok[indexA] = alapanyag;
        this.setState({ hozzavalok: hozzavalok });
    }
    changeHozzavaloNev = (id, event) => {
        const index = this.state.hozzavalok.findIndex((hozzavalo) => {
            return (hozzavalo.id === id);
        })
        const hozzavalo = Object.assign({}, this.state.hozzavalok[index]);
        hozzavalo.name = event.target.value;
        const hozzavalok = Object.assign([], this.state.hozzavalok);
        hozzavalok[index] = hozzavalo;
        this.setState({ hozzavalok: hozzavalok });
    }
    addStep() {
        const elkeszites = Object.assign({}, this.state.elkeszites);
        const steps = [...this.state.elkeszites.step, { id: this.state.elkeszites.counter, text: "" }];
        elkeszites.step = steps;
        elkeszites.counter = elkeszites.counter + 1;
        this.setState({ elkeszites: elkeszites });
    }
    removeStep = (indexH) => {
        const steps = Object.assign([], this.state.elkeszites.step);
        const elkeszites = Object.assign([], this.state.elkeszites);
        steps.splice(indexH, 1);
        elkeszites.step = steps;
        this.setState({ elkeszites: elkeszites });
    }
    changeStep = (id, event) => {
        const index = this.state.elkeszites.step.findIndex((step) => {
            return (step.id === id);
        })
        const elkeszites = Object.assign({}, this.state.elkeszites);
        elkeszites.step[index].text = event.target.value;
        this.setState({ elkeszites: elkeszites });
    }
    onSubmit(e) {
        e.preventDefault();
        this.setState({
            dialogOpen: true,
            dialogMsg: "Recept feltöltése...",
            okMsg: ""
        });
        this.uploadFile(this.state.fileData);
    }
    handleDialogClose() {
        this.setState({ dialogOpen: false });
    }
    getFile(file) {
        this.setState({ file: file });
    }
    getBase64(file) {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => resolve(reader.result);
            reader.onerror = error => reject(error);
        });
    }
    onaddfile(file) {
        if (file.fileSize > 3000000) {
            alert("Túl nagy kép, maximum méret: 3 MB");
            this.pond.removeFile();
        } else {
            this.setState({ fileData: file.file });
            this.getBase64(file.file).then(
                data => this.setState({ file: data })
            );
        }
    }
    uploadFile(file) {
        if (file === undefined) {
            this.uploadRecipeToDB();
        } else {
            var xhr = new XMLHttpRequest();
            var fd = new FormData();
            xhr.open('POST', Constans.ImageAPIUrl, true);
            xhr.setRequestHeader('X-Requested-With', 'XMLHttpRequest');


            // Update progress (can be used to show progress indicator)
            xhr.upload.addEventListener("progress", function (e) {
                var progress = Math.round((e.loaded * 100.0) / e.total);
                this.setState({ progValue: progress });
            }.bind(this));

            xhr.onreadystatechange = function (e) {
                if (xhr.readyState === 4) {
                    if (xhr.status === 200) {
                        // File uploaded successfully
                        var response = JSON.parse(xhr.responseText);
                        // https://res.cloudinary.com/cloudName/image/upload/v1483481128/public_id.jpg
                        var url = response.secure_url;
                        // Create a thumbnail of the uploaded image, with 150px width
                        this.setState({ fileID: response.public_id, fileUrl: url })
                        this.uploadRecipeToDB();
                    } else {
                        this.setState({
                            dialogMsg: "Nem tudtuk feltölteni, hiba történt kép feltöltés közben (Error: " + xhr.status + ").",
                            dialogOpen: true,
                            okMsg: "Ok"
                        });
                    }
                }
            }.bind(this);


            fd.append('upload_preset', 'r8sgbjtv');
            fd.append('file', file);
            fd.append('folder', Constans.cloudImageFolder);
            xhr.send(fd);
        }
    }
    uploadRecipeToDB() {
        const obj = {
            receptCim: this.state.receptCim,
            evszak: this.state.evszak,
            unnep: this.state.unnep,
            dieta: this.state.dieta,
            menu: this.state.menu,
            napszak: this.state.napszak,
            technika: this.state.technika,
            elkeszitesiIdo: this.state.elkeszitesiIdo,
            kaloria: this.state.kaloria,
            hozzavalok: this.state.hozzavalok,
            elkeszites: this.state.elkeszites,
            fileUrl: this.state.fileUrl,
            fileID: this.state.fileID
        };
        axios.post(Constans.APIUrl + 'recipe/add', obj)
            .then(
                res => {
                    if (res.status === 200) {
                        this.setState({
                            dialogMsg: "A receptet sikeresen feltöltöttük!",
                            okMsg: "Ok"
                        })

                    } else {
                        this.setState({ dialogMsg: "A feltöltés során hiba történt. Kérlek keresd Domit!" + res.status + ": " + res.statusText })
                    }

                    this.setState({ dialogOpen: true });
                    this.setState(clearRecipe());
                    this.pond.removeFile();
                }

            )
            .catch((error) => {
                const msg = "A feltöltés során hiba történt. Kérlek keresd Domit! ";
                if (error.response) {
                    this.setState({ dialogMsg: msg + error.response.status + ": " + error.response.statusText });
                } else {
                    // Something happened in setting up the request that triggered an Error
                    this.setState({ dialogMsg: msg + error.toString() });
                }
                this.setState({ dialogOpen: true });
            });
    }


    render() {
        const { classes } = this.props;
        return (
            <div className="all">
                <CustomDialog dialogOpen={this.state.dialogOpen}
                    dialogMsg={this.state.dialogMsg}
                    onClose={this.handleDialogClose.bind(this)}
                    okMsg={this.state.okMsg}
                    progValue={this.state.progValue} />

                <div className={getMobilOrBrowserSplit()} >
                    <Grid item xs={12} >
                        <Paper className={classes.paper} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                            <form onSubmit={this.onSubmit.bind(this)}>
                                <Grid item xs={12} style={gridSytle}>
                                    <NormalInput type="text" value={this.state.receptCim} onChange={this.onReceptCimChange.bind(this)} label="Recept címe" placeholder="Add meg a recept címét..." required={true} width="300%" margin="normal" />
                                </Grid>
                                <Grid item xs={12} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                                    <MultipleChoose checks={this.state.evszak} onChange={(label, e) => this.onEvszakChange(label, e)} title="Évszak:" labels={Constans.evszakLabels} />
                                </Grid>
                                <Grid item xs={12} style={gridSytle}>
                                    <MultipleChoose checks={this.state.unnep} onChange={(label, e) => this.onUnnepChange(label, e)} title="Ünnepek:" labels={Constans.unnepekLabels} />
                                </Grid>
                                <Grid item xs={12} style={gridSytle}>
                                    <MultipleChoose checks={this.state.dieta} onChange={(label, e) => this.onDietaChange(label, e)} title="Diéta:" labels={Constans.dietaLabels} />
                                </Grid>
                                <Grid item xs={12} style={gridSytle}>
                                    <MultipleChoose checks={this.state.menu} onChange={(label, e) => this.onMenuChange(label, e)} title="Menü:" labels={Constans.menuLabels} />
                                </Grid>
                                <Grid item xs={12} style={gridSytle}>
                                    <MultipleChoose checks={this.state.napszak} onChange={(label, e) => this.onNapszakChange(label, e)} title="Napszak:" labels={Constans.napszakLabels} />
                                </Grid>
                                <Grid item xs={12} style={gridSytle}>
                                    <MultipleChoose checks={this.state.technika} onChange={(label, e) => this.onTechnikaChange(label, e)} title="Technika:" labels={Constans.technikaLabels} />
                                </Grid>
                                <Grid item xs={12} style={gridSytle}>
                                    <FilePond ref={ref => this.pond = ref}
                                        onaddfile={(e, file) => {
                                            this.onaddfile(file)

                                        }}
                                        onremovefile={(e, file) => {
                                            this.setState({ file: "" });
                                        }}
                                        allowFileSizeValidation={true}
                                        maxFileSize="3MB"
                                        acceptedFileTypes='image/*'>
                                    </FilePond>
                                </Grid>
                                <Grid item xs={12} style={gridSytle}>
                                    <NormalInput adorment="perc" type="number" value={this.state.elkeszitesiIdo} onChange={this.onElkeszitesiIdoChange.bind(this)} label="Elkészítési idő (perc)" placeholder="Elékészítési idő" required={true} width='100%' margin="normal" />
                                </Grid>
                                <Grid item xs={12} style={gridSytle}>
                                    <NormalInput adorment="kcal" type="number" value={this.state.kaloria} onChange={this.onKaloriaChange.bind(this)} label="Kalória" placeholder="Kalória" required={true} width='100%' margin="normal" />
                                </Grid>
                                <Grid item xs={12} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                                    <Hozzavalok changeMennyiseg={(indexH, id, e) => this.changeMennyiseg(indexH, id, e)}
                                        changeAlapanyag={(indexH, id, e) => this.changeAlapanyag(indexH, id, e)}
                                        changeMertekegyseg={(indexH, id, e) => this.changeMertekegyseg(indexH, id, e)}
                                        removeAlapanyag={(indexH, indexA, e) => this.removeAlapanyag(indexH, indexA, e)}
                                        addAlapanyag={(indexH, e) => this.addAlapanyag(indexH, e)}

                                        changeHozzavaloNev={(indexH, e) => this.changeHozzavaloNev(indexH, e)}
                                        removeHozzavalo={(indexH, e) => this.removeHozzavalo(indexH, e)}
                                        addHozzavalo={() => this.addHozzavalo()}

                                        hozzavalok={this.state.hozzavalok}
                                    />
                                </Grid>
                                <Grid item xs={12} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>
                                    <Elkeszites elkeszites={this.state.elkeszites}
                                        addStep={() => this.addStep()}
                                        removeStep={(indexH) => this.removeStep(indexH)}
                                        onStepChange={(id, e) => this.changeStep(id, e)}
                                        title="Elkészítés: "
                                    />
                                </Grid>
                                <Grid item xs={12} style={{ marginTop: 20, marginLeft: 20, marginRight: 20 }}>

                                    <Button variant="contained" color="secondary" type="submit">
                                        Bevitel
                                </Button>
                                </Grid>
                            </form>
                        </Paper>
                    </Grid>
                </div>
                <BrowserView>
                    <div className="split right" >
                        <div className={classes.root}>
                            <RecipePage
                                state={this.state}
                                ownClassName={classes.paper}
                            />
                        </div>
                    </div>
                </BrowserView>
            </div>
        )
    }
}


Create.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Create);