import React, { Component } from "react";
import Create from './components/create.component';
import Edit from './components/edit.component';
import Search from './components/search.component';
import Login from './components/login.component';

import 'bootstrap/dist/css/bootstrap.min.css';

import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Divider from '@material-ui/core/Divider';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import InputIcon from '@material-ui/icons/Input';
import PowerSettingIcon from '@material-ui/icons/PowerSettingsNew';


import * as Constans from './Constans';
import axios from 'axios';
import { createStore, applyMiddleware, compose } from 'redux';


import { withStyles, MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import classNames from 'classnames';

const drawerWidth = 240;
const styles = theme => ({
  root: {
    display: 'flex',
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 20,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: '0 8px',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
});

const theme = createMuiTheme({
  palette: {
    primary: { main: '#2E3B55' }, // Purple and green play nicely together.
    secondary: { main: '#FD6A02' }, // This is just green.A700 as hex.
  },
  typography: { useNextVariants: true },
});

const ujRecept = 'Új recept';
const kereses = 'Keresés';
const szerkeztes = 'Szerkeztés';
export const login = 'Bejelentkezés';
const recipeDBToken = 'recipeDBToken';
const recipeDBUSer = 'recipeDBUSer';



function getChoosenComponent(inp) {
  const actTab = inp.state.actTab;

  if (inp.state.loggedIn) {
    if (actTab.localeCompare(ujRecept) === 0) {
      return (
        <Create />
      );
    } else if (actTab.localeCompare(szerkeztes) === 0) {
      return (
        <Edit />
      );
    } else if (actTab.localeCompare(kereses) === 0) {
      return (
        <Search />
      );
    }
  } else {
    return (
      <Login onSubmit={(event, user, psw, remember) => inp.login(event, user, psw, remember)} />
    );
  }
}


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      loggedIn: false,
      actTab: login,
      token: "",
    };

    this.loginNecessary(kereses);
  }

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };
  onItemClick = (item) => {
    this.setState({
      actTab: item,
      open: false
    });

  };

  login = (event, user, psw, remember) => {
    event.preventDefault();
    const obj = {
      user: user,
      psw: psw,
    };
    axios.post(Constans.APIUrl + 'recipe/login', obj)
      .then(
        res => {
          this.setState({
            token: res.data.token,
            loggedIn: true,
            actTab: kereses
          })
          if (remember) {
            localStorage.setItem(recipeDBToken, res.data.token);
            localStorage.setItem(recipeDBUSer, user);
          }
        }

      )
      .catch((error) => {

        console.log("6");
        this.setState({ loggedIn: false })
      });

  };

  loggingIcon = () => {
    if (!this.state.loggedIn) {
      return (
        <ListItem button key='Bejelentkezés' onClick={this.onItemClick.bind(this, login)}>
          <ListItemIcon><InputIcon /></ListItemIcon>
          <ListItemText primary='Bejelentkezés' />
        </ListItem>);
    } else {
      return (
        <ListItem button key='Kijelentkezés' onClick={this.logout}>
          <ListItemIcon><PowerSettingIcon /></ListItemIcon>
          <ListItemText primary='Kijelentkezés' />
        </ListItem>
      )
    }
  }

  loginNecessary = (tab) => {

    if (this.state.loggedIn) {
      this.setState({ open: false });
      this.onItemClick.bind(this, tab)
    } else {
      const token = localStorage.getItem(recipeDBToken);
      const user = localStorage.getItem(recipeDBUSer);
      const obj = {
        user: user,
        token: token,
      };
      axios.post(Constans.APIUrl + 'recipe/token', obj)
        .then(
          res => {
            this.setState({
              token: res.token,
              loggedIn: true,
              actTab: tab,
              open: false
            })

          }

        )
        .catch((error) => {
          localStorage.setItem(recipeDBToken, "");
          localStorage.setItem(recipeDBUSer, "");
          this.setState({
            open: false,
            actTab: login
          });
        });

    }
  };

  logout = () => {
    this.setState({
      token: "",
      loggedIn: false,
      actTab: login,
      open: false
    })
    localStorage.setItem(recipeDBToken, "");
    localStorage.setItem(recipeDBUSer, "");

  };


  render() {
    const { classes } = this.props;
    const { open } = this.state;
    return (

      <MuiThemeProvider theme={theme}>
        <div className={classes.root}>
          <CssBaseline />
          <AppBar
            position="fixed"
            className={classNames(classes.appBar, {
              [classes.appBarShift]: open,
            })}
          >
            <Toolbar disableGutters={!open}>
              <IconButton
                color="inherit"
                aria-label="Open drawer"
                onClick={this.handleDrawerOpen}
                className={classNames(classes.menuButton, open && classes.hide)}
              >
                <MenuIcon />
              </IconButton>
              <Typography variant="h6" color="inherit" noWrap>
                {this.state.actTab}
              </Typography>
            </Toolbar>
          </AppBar>
          <Drawer
            className={classes.drawer}
            variant="persistent"
            anchor="left"
            open={open}
            classes={{
              paper: classes.drawerPaper,
            }}
          >
            <div className={classes.drawerHeader}>
              <IconButton onClick={this.handleDrawerClose}>
                {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
              </IconButton>
            </div>
            <Divider />
            <List>
              <ListItem key={kereses} onClick={this.onItemClick.bind(this, kereses)} button>
                <ListItemIcon><SearchIcon /></ListItemIcon>
                <ListItemText primary={kereses} />
              </ListItem>
              <ListItem key={szerkeztes} onClick={this.onItemClick.bind(this, szerkeztes)} button>
                <ListItemIcon><EditIcon /></ListItemIcon>
                <ListItemText primary={szerkeztes} />
              </ListItem>
              <ListItem key={ujRecept} onClick={this.onItemClick.bind(this, ujRecept)} button>
                <ListItemIcon><AddIcon /></ListItemIcon>
                <ListItemText primary={ujRecept} />
              </ListItem>
              <Divider />
              {this.loggingIcon()}
            </List>
          </Drawer>

          <main
            onClick={()=>this.setState({open: false})}
            className={classNames(classes.content, {
              [classes.contentShift]: open,
            })}
          >
            <div className={classes.drawerHeader} />
            {getChoosenComponent(this)}
          </main>

        </div>
      </MuiThemeProvider>
    );
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(App);

